$container-side-padding: 16px;

html, body {
  min-height: 100vh;
  margin-left: calc(100vw - 100%);
}

body {
  margin: 0;
  color: $color-white;
  background: $color-darken-gray;
}

.container {
  max-width: 700px - $container-side-padding * 2;
  margin: auto;
  padding-right: $container-side-padding;
  padding-left: $container-side-padding;
}
