// Reference palette https://colorhunt.co/palette/219713


$color-map: (
  "darken-yellow": $color-darken-yellow,
  "darken-gray": $color-darken-gray,
  "yellow": $color-yellow,
  "white": $color-white,
  "black": $color-black,
  "gray": $color-gray,
);

.palette {
  @each $name, $value in $color-map {
    &-tx-#{$name} {
      color: $value;
    }

    &-bg-#{$name} {
      background-color: $value;
    }
  }
}
