//            1   2    3    4    5    6    7c
$margin-list: 8px 16px 24px 32px 40px 64px 2px;

@for $i from 1 through length($margin-list) {
    .mt-#{$i} {
        margin-top: nth($margin-list, $i);
    }
    .mb-#{$i} {
        margin-bottom: nth($margin-list, $i);
    }
    .ml-#{$i} {
        margin-left: nth($margin-list, $i);
    }
    .mr-#{$i} {
        margin-right: nth($margin-list, $i);
    }
    .mx-#{$i} {
        margin-right: nth($margin-list, $i);
        margin-left: nth($margin-list, $i);
    }
    .my-#{$i} {
        margin-top: nth($margin-list, $i);
        margin-bottom: nth($margin-list, $i);
    }
    .ma-#{$i} {
        margin: nth($margin-list, $i);
    }

    .pt-#{$i} {
        padding-top: nth($margin-list, $i);
    }
    .pb-#{$i} {
        padding-bottom: nth($margin-list, $i);
    }
    .pl-#{$i} {
        padding-left: nth($margin-list, $i);
    }
    .pr-#{$i} {
        padding-right: nth($margin-list, $i);
    }
    .px-#{$i} {
        padding-right: nth($margin-list, $i);
        padding-left: nth($margin-list, $i);
    }
    .py-#{$i} {
        padding-top: nth($margin-list, $i);
        padding-bottom: nth($margin-list, $i);
    }
    .pa-#{$i} {
        padding: nth($margin-list, $i);
    }


}

