    url('../../../assets/fonts/bicons.ttf?ubu5h6') format('truetype'),
    url('../../../assets/fonts/bicons.woff?ubu5h6') format('woff'),
    url('../../../assets/fonts/bicons.svg?ubu5h6#bicons') format('svg');
  font-display: block;
}

.i {
  font-weight: normal;
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'bicons' !important;
  font-style: normal;
  font-variant: normal;
  line-height: 1;
  text-transform: none;
  speak: never;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

